<template>
  <div id="main">
    <div class="logo">logo</div>
    <div class="search">
      <form>
        <input type="search" placeholder="Search">
      </form>
    </div>
    <login class="login" v-if="accessToken===''"></login>
    <lk class="lk" v-else></lk>
  </div>
</template>

<script>
  import login from './auth/login.vue';
  import lk from './auth/lk.vue';


  export default {
    data () {
      return {
        msg:'This is Header',
        barer:'',
        accessToken:'',
        refreshToken:''
      }
    },
    components:{
      login,
      lk
    }
  }
</script>

<style>

  div {
    text-align: center;
  }
  #main{
    width: 100vw;
    display: table;
  }
  #main>div{
    display: table-cell;
    text-align: center;
    vertical-align: middle;
  }
  #main>.login{
    display: table-cell;
    text-align: center;
    vertical-align: middle;
  }
  #main>.lk{
    display: table-cell;
    text-align: center;
    vertical-align: middle;
  }

  .logo{
    width: 10vw;
  }
  .search{
    width: 60vw;
  }
  .search>form>input{
    width: 100%;
  }
  .lk{
    width: 30vw;
  }
</style>



