<template>
  <div>
    <input v-model="login" placeholder="login">
    <input v-model="password" placeholder="password">
    <input type="button" value="login" @click="LogIn(login,password)">
  </div>
</template>

<script>
  const axios = require('axios');

  export default {
    data () {
      return {
        login:'',
        password:''
      }
    },
    methods:{
      LogIn(login,pass){
        axios({
          method:'post',
          url:'http://localhost:8888/oauth/token',
          headers:{
            'Content-Type':'application/x-www-form-urlencoded',
            'Authorization':'Basic VVNFUl9DTElFTlRfQVBQOnBhc3N3b3Jk'
          },
          data:{
            grant_type:'password',
            username:login,
            password: pass
            }
          }
        ).then(function (response) {
          console.log(response) // someValue
        }).catch(function (error) {
          console.log(error) // someValue
        });
      }
    }
  }
</script>

<style>

</style>



