<template>
  <div id="app">
    <MuseHeader class="header"></MuseHeader>
    <MusePanel class="panel"></MusePanel>
    <MuseFooter class="footer"></MuseFooter>
  </div>
</template>

<script>
import MuseHeader from './components/museheader.vue';
import MuseFooter from './components/musefooter.vue';
import MusePanel from './components/musepanel.vue';

export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      barer:'',
      accessToken:'',
      refreshToken:''
    }
  },
  components:{
    MuseHeader,
    MuseFooter,
    MusePanel
  }
}
</script>

<style>
  .panel{
    position: fixed; /* Фиксированное положение */
    left: 0; top: 7vh; /* Левый нижний угол */
    margin: 0;
    height: 83vh;
    background: #fff; /* Цвет фона */
    color: #000; /* Цвет текста */
    width: 100%; /* Ширина слоя */
    overflow:scroll;
  }
  .header{
    position: fixed; /* Фиксированное положение */
    left: 0; top: 0; /* Левый нижний угол */
    margin: 0;
    height: 7vh;
    background: #39b54a; /* Цвет фона */
    color: #fff; /* Цвет текста */
    width: 100%; /* Ширина слоя */
  }
.footer{
  position: fixed; /* Фиксированное положение */
  left: 0; bottom: 0; /* Левый нижний угол */
  margin: 0;
  height: 10vh;
  background: #39b54a; /* Цвет фона */
  color: #fff; /* Цвет текста */
  width: 100%; /* Ширина слоя */
}

</style>



